// @import "mixins";
@import "generated/sprite-svg";
body {
  background-color: rgba(175, 211, 154, 0.28);
}

.acn {
  width: 100%;
  .card {
    border: none;
    background-color: transparent;
  }
  .card-header {
    display: flex;
    align-items: center;
    padding: 0;
    border-radius: none;
    border-bottom: none;
    background-color: transparent;
  }
  .card-body {
    padding: 0 0 0 125px;
    color: #46503e;
    font-family: "CorporateSPro", Arial, sans-serif;
    font-size: 1rem;
    font-weight: normal;
    @media only screen and (max-width: 768px) {
      padding-left: 0px;
      font-size: 0.8rem;
    }
  }
}

.acn-title {
  width: 100%;
  text-transform: uppercase;
  color: #77ad4b;
  a {
    display: flex;
    align-items: center;
    color: inherit;
    font-size: 1.2rem;
    &:hover {
      text-decoration: none;
    }
    @media only screen and (max-width: 768px) {
      font-size: 1rem;
    }
    .icon {
      transition: all 0.2s ease;
    }
  }
  &:hover {
    color: darken(#77ad4b, 8%);
    .icon {
      fill: darken(#77ad4b, 8%);
    }
  }
}

.acn-title-text {
  align-self: stretch;
  flex-grow: 1;
  display: flex;
  align-items: center;
  padding-left: 5px;
  border-bottom: 2px solid #93c071;
  transition: all 0.2s ease;
}

.acn-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  min-height: 64px;
  min-width: 105px;
  @media only screen and (max-width: 768px) {
    min-width: 80px;
  }
  .icon {
    fill: #77ad4b;
    max-width: 81px;
    transition: all 0.2s ease;
  }
  &.euro {
    font-family: "CorporateSPro", Arial, sans-serif;
    font-size: 41px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    color: #77ad4b;
  }
}

.acn-sign {
  position: relative;
  width: 21px;
  height: 21px;
  line-height: 1;
  font-family: "CorporateSPro", Arial, sans-serif;
  font-size: 41px;
  font-weight: bold;
  font-style: normal;
  @media only screen and (max-width: 768px) {
    font-size: 30px;
  }
}

.acn-sign-minus,
.acn-sign-plus {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

a[aria-expanded="true"] {
  .acn-title-text {
    border-color: transparent;
  }
  .acn-sign-plus {
    opacity: 0;
  }
}

a[aria-expanded="false"] {
  .acn-sign-minus {
    opacity: 0;
  }
}

.acn-title-price {
  margin-left: auto;
  color: #46503e;
  font-size: 1rem;
  font-weight: normal;
  @media only screen and (max-width: 768px) {
    font-size: 0.8rem;
  }
  span {
    padding-left: 10px;
    padding-right: 5px;
  }
}

